
@font-face {
  font-family: 'clementepdaelight';
  src: url(/static/fonts/clementepdae-light-webfont.2bdceec.woff2) format('woff2'),
  url(/static/fonts/clementepdae-light-webfont.1e4bdb8.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'clementepdairegular';
  src: url(/static/fonts/clementepdai-regular-webfont.8f399ef.woff2) format('woff2'),
  url(/static/fonts/clementepdai-regular-webfont.65e944d.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}
body, html {
  height:98%;
}
#app {
  font-family: 'clementepdaelight', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 2%;
  color: black;
  min-height:100%;
  margin:0 auto;
  position:relative;
}
.quezeBold {
  font-family: 'clementepdairegular', 'Avenir', Helvetica, Arial, sans-serif;
}
#quezel-logo {
  width: 20%;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  width: 11.5%;
  text-transform: uppercase;
}
h1 {
  text-transform: uppercase;
  font-size: 1.5em;
}
#quezel-menu a, .column a {
  color: gray;
  text-decoration: none;
}
#quezel-menu li {
  color: gray;
}
.router-link-exact-active, .column a:hover {
  color: black;
  font-family: 'clementepdairegular', 'Avenir', Helvetica, Arial, sans-serif;
}
li.dropdown.selected {
  color: black !important;
  font-family: 'clementepdairegular', 'Avenir', Helvetica, Arial, sans-serif;
}
#quezel-menu {
  display: inline-block;
  width: 100%;
  padding-top: 16px;
}
.page-content {
  margin-top: 2%;
  padding-bottom: 80px;
}
.text-description {
  display: inline-block;
  text-align: justify;
  width: 51.5%;
  font-size: 0.9em;
}
.agence .text-description {
  width: 20%;
}
#quezel-bottom-page {
  margin-top: 2%;
  position:absolute;
  bottom:0;
  width: 100%;
  font-size: 0.75em;
}
.page-content.contact .text-description {
  text-align: center;
}
.dropdown .dropbtn {
  border: none;
  outline: none;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}
.dropdown-content a {
  float: none;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}
.dropdown-content a:hover {
  background-color: #ddd;
}
.dropdown .dropdown-content.open {
  display: block;
}


  /*
  Vue image Grid
   */
.row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
}

/* Create four equal columns that sits next to each other */
.column {
  flex: 25%;
  max-width: 25%;
  padding: 0 4px;
}
.detailProjet .column {
  flex: 100%;
  max-width: 100%;
  padding: 0 4px;
}
.column img {
  margin-top: 8px;
  vertical-align: middle;
  width: 100%;
  max-height: 800px;
}
.column.clickable img:not(:hover) {
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}

/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 800px) {
.column {
    flex: 50%;
    max-width: 50%;
}
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
.column {
    flex: 100%;
    max-width: 100%;
}
}
.img-inlineblock-vue img {
  width : 100%;
  margin-top: 0.67em;
}
.separator-vue {
    background-color: lightgrey;
    height: 1px;
    margin-top: 40px;
    margin-bottom: 28px;
    width: 20%;
    text-align: center;
    display: inline-block;
}











































































































































